/** UI **/
/** Media Queries **/
.features {
  text-align: center;
  height: 100vh;
  background-color: #88a8a8;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 767px) {
  .features {
    height: 100%;
    padding: 5rem 0;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .features {
    height: 100%;
    padding: 5rem 0;
  }
}
.features h2 {
  margin-bottom: 2rem;
  width: auto;
  color: #fff;
  margin-bottom: 3rem;
}
@media (max-width: 767px) {
  .features h2 {
    font-size: 4rem;
  }
}
.features .feature-cards {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-top: 2rem;
}
@media (max-width: 767px) {
  .features .feature-cards {
    row-gap: 4rem;
  }
}
.features .feature-cards .card {
  text-align: center;
  position: relative;
  border-radius: 100%;
  overflow: hidden;
  width: 35rem;
  height: 35rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}
.features .feature-cards .card:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
.features .feature-cards .card picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.features .feature-cards .card picture .card-bg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.features .feature-cards .card .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Oscurece la imagen */
  z-index: 1;
}
.features .feature-cards .card .info {
  position: relative;
  z-index: 2;
  color: #fff;
  padding: 1rem;
  text-align: center;
}
.features .feature-cards .card .info h3 {
  font-size: 3rem;
  margin-bottom: 10px;
}
.features .feature-cards .card .info p {
  margin-top: 1.2rem;
  font-size: 2rem;
  color: #fff;
}

/** Media Queries **/
footer {
  background-color: #2c3e50;
  color: #ecf0f1;
  text-align: center;
  height: 100vh;
}
@media (max-width: 767px) {
  footer {
    width: 100%;
    height: 100%;
    padding-top: 5rem;
  }
}
footer .footer-content {
  font-size: 2.2rem;
}
footer .footer-content .social-links {
  display: flex;
  justify-content: center;
  row-gap: 2rem;
  flex-direction: column;
  height: 28vh;
}
footer .footer-content .social-links .social-icon-in,
footer .footer-content .social-links .social-icon-wt {
  color: #ecf0f1;
  text-decoration: none;
  transition: color 0.3s;
}
footer .footer-content .social-links .social-icon-in i,
footer .footer-content .social-links .social-icon-wt i {
  margin-right: 0.5rem;
}
footer .footer-content .social-links .social-icon-wt:hover {
  color: #1abc9c;
}
footer .footer-content .social-links .social-icon-in:hover {
  color: #e0441d !important;
}
footer .footer-content p {
  margin: 0 0 20px;
}
footer .footer-content #google-map {
  width: 96%;
  height: 65vh;
  margin: 0 auto;
  border: none;
}
@media (max-width: 767px) {
  footer .footer-content #google-map {
    height: 40rem;
    margin-top: 2rem;
  }
}
footer .footer-bottom {
  text-align: right;
  height: 6vh;
  padding: 2vh 2rem;
}
footer .footer-bottom p {
  font-size: 1.5rem;
}
@media (max-width: 767px) {
  footer .footer-bottom {
    text-align: center;
    font-size: 1rem;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  footer .footer-bottom {
    text-align: right;
    font-size: 1.2rem;
  }
}

/** Media Queries **/
/* Variables */
/* Header */
header {
  background-color: hsl(186, 19%, 21%);
  color: #fff;
  height: 10vh;
  font-size: 2rem;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  box-shadow: -1px 4px 5px 0px rgba(0, 0, 0, 0.38);
  -webkit-box-shadow: -1px 4px 5px 0px rgba(0, 0, 0, 0.38);
  -moz-box-shadow: -1px 4px 5px 0px rgba(0, 0, 0, 0.38);
}
@media (max-width: 767px) {
  header {
    position: fixed;
    width: 100%;
    height: 10%;
    z-index: 3;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  header {
    height: 10%;
    width: 100%;
  }
}
header nav {
  display: flex;
  width: 90%;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
}
header nav .logo {
  font-size: 1.5em;
}
header nav ul {
  list-style: none;
  display: flex;
  width: 40%;
  justify-content: space-around;
}
@media (min-width: 768px) and (max-width: 1023px) {
  header nav ul {
    width: 60%;
  }
}
header nav ul li a {
  color: #fff;
  text-decoration: none;
}
header nav ul li a:hover {
  text-shadow: 0px 4px 4px rgb(0, 0, 0);
}

/* Icono de menú hamburguesa */
.menu-icon {
  display: none;
  /* Oculto por defecto */
  flex-direction: column;
  gap: 0.7rem;
  cursor: pointer;
}
.menu-icon .line {
  width: 4.5rem;
  height: 0.7rem;
  background-color: #fff;
  border-radius: 2px;
}

/* Menú móvil */
.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsl(186, 19%, 21%);
  color: #fff;
  display: none;
  /* Oculto por defecto */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100;
}
.menu-overlay .menu-content {
  text-align: center;
  height: 50%;
}
.menu-overlay .menu-content .close-btn {
  font-size: 5rem;
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}
.menu-overlay .menu-content h2 {
  margin-bottom: 1rem;
  text-shadow: 4px 4px 2px rgba(0, 0, 0, 0.6);
}
.menu-overlay .menu-content ul {
  list-style: none;
  padding: 0;
  width: 100%;
}
.menu-overlay .menu-content ul li {
  margin: 3rem 0;
}
.menu-overlay .menu-content ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 3rem;
}
.menu-overlay .menu-content ul li a:hover {
  text-shadow: 0px 4px 4px rgb(0, 0, 0);
}

/* Mostrar el menú al abrir */
.menu-overlay.open {
  display: flex;
  width: 100%;
  /* Ajustar al ancho completo */
  height: 100%;
  /* Ajustar al alto completo */
}

/* Media Queries */
@media (max-width: 768px) {
  nav ul > li > a {
    display: none;
    /* Ocultar el menú tradicional */
  }
  .menu-icon {
    display: flex;
    /* Mostrar menú hamburguesa */
  }
}
/** Media Queries **/
/* Hero Section */
.hero {
  position: relative;
  height: 90vh;
  overflow: hidden;
  text-align: center;
  color: #fff;
}
.hero video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  left: 0%;
}
@media (max-width: 767px) {
  .hero {
    height: 100vh;
  }
}
.hero .hero-overlay {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 110%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.hero .hero-overlay h1, .hero .hero-overlay p {
  width: auto;
}
.hero .hero-overlay h1 {
  font-size: 5rem;
}
.hero .hero-overlay p {
  margin-top: 2rem;
  font-size: 3rem;
}
.hero .hero-overlay p strong {
  font-weight: bold;
}

/** Paginas Internas **/
/** Media Queries **/
body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
  color: #333;
}

.about-us .section {
  padding: 2rem;
  width: 100%;
  height: 100%;
  text-align: center;
}
.about-us .section h2 {
  margin-bottom: 3rem;
}
.about-us .section p {
  margin-top: 1rem;
  margin-bottom: 3rem;
}
@media (max-width: 767px) {
  .about-us .section p {
    font-size: 2rem;
  }
}
.about-us .section .content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 1023px) {
  .about-us .section .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.about-us .section .content .who-we-are__img {
  width: 55rem;
  height: 55rem;
  object-fit: cover;
  object-position: center;
  border-radius: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  margin: 0 auto 3rem 0;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}
.about-us .section .content .who-we-are__img:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .about-us .section .content .who-we-are__img {
    width: 35rem;
    height: 35rem;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .about-us .section .content .who-we-are__img {
    width: 45rem;
    height: 45rem;
  }
}
.about-us .section .content .why-choose-us__img {
  width: 55rem;
  height: 55rem;
  object-fit: cover;
  object-position: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .about-us .section .content .why-choose-us__img {
    width: 35rem;
    height: 35rem;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .about-us .section .content .why-choose-us__img {
    width: 45rem;
    height: 45rem;
  }
}
.about-us .who-we-are {
  background-color: hsl(186, 19%, 21%);
  color: #fff;
}
@media (max-width: 1023px) {
  .about-us .who-we-are h2 {
    margin-top: 10vh;
  }
}
.about-us .our-mission {
  background-color: #698C8C;
  color: #fff;
}
.about-us .why-choose-us {
  background-color: #F2F2F2;
  color: #294c4a;
}

.container-carousel {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 1023px) {
  .container-carousel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

/* Carrusel */
.carousel {
  position: relative;
  max-width: 100%;
  margin: auto;
  overflow: hidden;
}

/* Estilo para las imágenes */
.carousel-images {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.carousel-images img {
  width: 55rem;
  height: 55rem;
  object-fit: cover;
}
@media (max-width: 767px) {
  .carousel-images img {
    width: 35rem;
    height: 35rem;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .carousel-images img {
    height: 45rem;
    width: 45rem;
  }
}

.carousel-images picture {
  flex: 0 0 100%;
  display: none;
}

/* Estilo para los puntos de navegación */
.carousel-dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  top: 90%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.dot {
  width: 12px;
  height: 12px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dot.active {
  background-color: #fff;
}

.dot:hover {
  background-color: #ddd;
}

/* Estilos de transición suave */
.carousel-images picture {
  transition: opacity 1s ease;
}

.carousel-images picture:first-child {
  display: block;
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .carousel {
    width: 100%;
  }
  .carousel-images picture {
    width: 100%;
  }
}
.content p,
.container-carousel p {
  font-size: 2.2rem;
  line-height: 1.6;
  width: 100%;
  transition: all 0.3s ease-in-out;
  margin: 0 auto;
}
.content p:hover,
.container-carousel p:hover {
  text-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
  transform: scale(1.05);
}

/* Variables */
/** Media Queries **/
.intro {
  padding: 5rem;
  width: 90%;
}
.intro h1 {
  padding-top: 1rem;
}

.services {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* Dos columnas */
  gap: 2rem;
  padding-bottom: 5rem;
}
@media (max-width: 1023px) {
  .services {
    display: flex;
    flex-direction: column;
  }
}
.services .service-item {
  width: 90%;
  /* Ajusta el ancho para que se vea más centrado */
  margin: 0 auto;
  /* Centrar los ítems */
  /* Mostrar el contenido cuando se activa */
}
.services .service-item .dropdown {
  display: flex;
  /* Alinear los elementos en línea */
  align-items: center;
  /* Centrar verticalmente */
  justify-content: space-between;
  /* Separar texto y flecha */
  padding: 10px;
  background-color: #88a8a8;
  /* Color del fondo */
  color: #F2F2F2;
  cursor: pointer;
  /* Cambiar cursor al pasar */
  border: none;
  /* Quitar bordes del botón */
  width: 100%;
  /* Que ocupe todo el ancho disponible */
  font-family: Arial, sans-serif;
  /* Fuente estándar */
  font-size: 16px;
  /* Tamaño del texto */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /* Sombra sutil */
}
.services .service-item .dropdown:hover {
  transition: background-color 0.5s; /* Transición de color en 1 segundo */
  background-color: #294c4a;
}
.services .service-item .dropdown img.texto {
  width: auto;
  /* Tamaño del ícono */
  height: 2.5rem;
  margin-right: 1rem;
  /* Separación del texto */
}
.services .service-item .dropdown p {
  flex: 1;
  /* El texto ocupa el espacio restante */
  margin: 0;
  /* Elimina márgenes del párrafo */
  text-align: left;
  /* Alinea el texto a la izquierda */
}
.services .service-item .dropdown img.flecha {
  width: 20px;
  /* Tamaño de la flecha */
  height: auto;
  margin-left: 10px;
  /* Separación del texto */
  transition: transform 0.3s ease;
  /* Animación suave */
}
.services .service-item .dropdown.active img.flecha {
  transform: rotate(180deg);
  /* Flecha hacia arriba */
}
.services .service-item .dropdown-content {
  display: none;
  /* Ocultar el contenido por defecto */
  background-color: #F2F2F2;
  padding: 10px;
  margin-top: 5px;
  font-size: 14px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /* Sombra sutil */
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
}
@media (max-width: 767px) {
  .services .service-item .dropdown-content {
    flex-direction: column;
  }
}
.services .service-item .dropdown-content img {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .services .service-item .dropdown-content img {
    padding-top: 1rem;
  }
}
.services .service-item .dropdown.active .dropdown-content {
  display: grid;
  /* Mostrar contenido si el botón tiene clase 'active' */
}
@media (max-width: 767px) {
  .services .service-item .dropdown.active .dropdown-content {
    display: flex;
  }
}